* {
  box-sizing: border-box;
  color: #fff;
  margin: 0;
  padding: 0;
}

body {
  background-color: #000;
  font-size: 16px;
  overflow: hidden;
}

#board {
  margin: 40px auto;
}

.cell {
  width: 40px;
  height: 40px;
  background: rgba(255, 255, 255, .2);
}

.cell.snake {
  background-color: green;
}

.cell.food {
  position: relative;
}

.cell.food:after {
  content: "";
  height: 100%;
  width: 100%;
  background-image: url("https://cdn-icons-png.flaticon.com/512/2821/2821784.png");
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.row {
  justify-content: center;
  align-items: center;
  display: flex;
}

